@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  padding: 0 15px;
  font-family: 'Source Sans Pro', sans-serif;
  background: linear-gradient(to right, #1bdedb, #6c1ce3);
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

.title {
  font-size: 45px;
  text-align: center;
}

@media screen and (min-width: 300px) {
  .title {
    font-size: 55px;
  }
}

.searchbox-container{text-align:center}.searchbox-container input{padding:15px 20px;border-radius:5px;outline:none;border:none}

.cardlist-container{max-width:1300px;margin:50px auto;display:grid;grid-template-columns:repeat(1, 1fr);grid-gap:15px}@media screen and (min-width: 450px){.cardlist-container{grid-template-columns:repeat(2, 1fr)}}@media screen and (min-width: 768px){.cardlist-container{grid-template-columns:repeat(3, 1fr)}}@media screen and (min-width: 1024px){.cardlist-container{grid-template-columns:repeat(4, 1fr)}}

.card-container{text-align:center;border-radius:10px;cursor:pointer;background:#2ac1be;padding-bottom:30px;box-shadow:0 0 25px 1px rgba(0,0,0,0.15);transition:transform 0.5s}.card-container:hover{transform:scale(1.05)}

